@import url("https://fonts.googleapis.com/css2?family=Quicksand:wght@300;500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@200;700&display=swap");
.page-title {
  font-size: 3.5rem;
  color: #6458F5;
  font-family: "Montserrat", sans-serif;
}

.page-sub-title {
  font-size: 1.5rem;
  color: #B2ACFA;
  font-family: "Montserrat", sans-serif;
}

.page-paragraph {
  font-size: 1rem;
  color: #868686;
  font-family: "Quicksand", sans-serif;
}

.item-title {
  font-size: 2rem;
  color: #6458F5;
  font-family: "Montserrat", sans-serif;
  padding: 1rem 0rem 1rem 0rem;
}

* {
  box-sizing: border-box;
  margin: 0;
}

#app {
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.page-container {
  flex: 1 0 auto;
}

footer {
  flex-shrink: 0;
}

.card-container {
  display: flex;
  justify-content: center;
  width: auto;
  flex-direction: column;
  text-align: left;
  padding: 1rem;
  border: none;
  border-radius: 6px;
  margin: 4rem;
  box-shadow: 0 0 20px #d4d4d4;
}
@media (min-width: 768px) {
  .card-container {
    width: 41.6666666667%;
  }
}

.card-text-container {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.card-title {
  font-size: 2.5rem;
  color: white;
  padding-bottom: 1rem;
  font-family: "Montserrat", sans-serif;
}

.card-sub-title {
  color: white;
  font-family: "Montserrat", sans-serif;
}

.card-text {
  color: white;
  font-family: "Quicksand", sans-serif;
}

.button-inline-container {
  display: flex;
  display: inline-block;
  padding: 1rem;
}

.card-img {
  width: auto;
  border-radius: 5px;
  height: 20rem;
  object-fit: cover;
}

.card-container-horizontal {
  display: flex;
  justify-content: center;
  width: 20rem;
  flex-direction: column;
  text-align: left;
  padding: 1rem;
  border: none;
  border-radius: 6px;
  margin: 4rem;
  box-shadow: 0 0 20px #d4d4d4;
}
@media (min-width: 768px) {
  .card-container-horizontal {
    width: 41.6666666667%;
  }
}

.form-title {
  color: #333;
  font-family: #6458F5;
  font-size: x-large;
  font-weight: bold;
}

header {
  height: 100px;
  background-color: #FBE092;
  display: flex;
  justify-content: space-between;
}

.logo-image {
  width: 15rem;
  height: 100%;
}
@media (min-width: 768px) {
  .logo-image {
    padding-left: 3rem;
    width: 20rem;
  }
}

.nav {
  width: auto;
}

.nav-link {
  display: none;
  font-size: 1.5rem;
  font-family: "Quicksand", sans-serif;
  color: #6458F5;
  text-decoration: none;
}
.nav-link:hover {
  color: #6458F5;
}
@media (min-width: 768px) {
  .nav-link {
    display: block;
    padding-left: 1rem;
    font-size: 1rem;
  }
}

.hamburger {
  cursor: pointer;
  color: #fff;
  font-size: 2.5rem;
  height: 100%;
  display: flex;
  align-items: center;
  padding-right: 1rem;
}
.hamburger:hover {
  color: #6458F5;
}
@media (min-width: 768px) {
  .hamburger {
    display: none;
  }
}

.close-btn {
  cursor: pointer;
  color: #fff;
  font-size: 3rem;
  position: absolute;
  top: 22px;
  right: 12px;
}
.close-btn:hover {
  color: #6458F5;
}
@media (min-width: 768px) {
  .close-btn {
    display: none;
  }
}

.nav-menu {
  height: 100%;
  display: flex;
  background-color: #FBE092;
}
@media (min-width: 768px) {
  .nav-menu {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
    padding-right: 3rem;
  }
}

.nav-menu-toggle {
  background-color: #FBE092;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  width: 100vw;
  height: 50vh;
  z-index: 100;
  animation: growDown 300ms ease-in-out forwards;
  transform-origin: top center;
  position: relative;
  left: -240px;
  top: 0;
}
@keyframes growDown {
  0% {
    transform: scaleY(0);
  }
  80% {
    transform: scaleY(1.1);
  }
  100% {
    transform: scaleY(1);
  }
}
.nav-menu-toggle .nav-link {
  display: block;
}
@media (min-width: 768px) {
  .nav-menu-toggle {
    display: none;
  }
}

.container {
  display: flex;
  justify-content: center;
  position: relative;
  max-width: 100%;
  padding: 0 3rem;
}

.flex-container {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .flex-container {
    flex-direction: row;
  }
}

.flex-column {
  flex-direction: column;
}
.flex-column-reverse {
  flex-direction: column-reverse;
}

.flex-row {
  flex-direction: row;
}
.flex-row-reverse {
  flex-direction: row-reverse;
}

.item {
  order: 0;
  font-size: 1rem;
  max-width: 100%;
  margin: 4rem 0;
}
@media (min-width: 1020px) {
  .item {
    font-size: 2rem;
  }
}

.home-container {
  width: 100%;
}
@media (min-width: 768px) {
  .home-container {
    width: 50%;
    padding: 1rem;
  }
}

.column-9 {
  width: 100%;
}
@media (min-width: 768px) {
  .column-9 {
    width: 75%;
  }
}

.scroll {
  overflow-y: scroll;
  max-height: 500px;
}

.items-wrapper {
  width: 900px;
}

.item-container {
  margin-top: 40px;
  padding: 1rem 2rem 1rem 2rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 0 20px #d4d4d4;
  border-radius: 5px;
}
@media (max-width: 767px) {
  .item-container {
    flex-direction: column;
    padding: 10px;
  }
}

.item-details {
  line-height: 2;
}

.item-img > img {
  width: 260px;
  height: 170px;
  box-shadow: 0 0 20px #d4d4d4;
  border-radius: 5px;
}
@media (max-width: 767px) {
  .item-img > img {
    flex-direction: column;
    padding: 10px;
  }
}

.about-page-container {
  text-align: center;
  margin: 30px auto;
  position: relative;
  max-width: 100%;
  padding: 0 3rem;
  font-family: "Quicksand", sans-serif;
}

.flex-container {
  margin: auto;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .flex-container {
    flex-direction: row;
  }
}

.flex-column {
  flex-direction: column;
}
.flex-column-reverse {
  flex-direction: column-reverse;
}

.flex-row {
  margin: 30px auto;
  text-align: center;
  width: 300px;
  flex-direction: row;
}
.flex-row-reverse {
  flex-direction: row-reverse;
}
.flex-row a:link, .flex-row a:visited {
  margin: 20px;
  background-color: #6458F5;
  color: white;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
}
.flex-row a:link:hover, .flex-row a:visited:hover {
  background-color: #333;
}

.img-holder {
  width: 300px;
  border-radius: 50%;
  height: 300px;
  margin: 20px auto;
  border: 2px solid #6458F5;
}

.img-holdersmall {
  width: 220px;
  border-radius: 50%;
  height: 220px;
  margin: 20px auto;
  border: 2px solid #6458F5;
}

.horizontal-line {
  height: 0.1666666667rem;
  width: 4rem;
  background-color: #B2ACFA;
  align-self: center;
  margin-top: 1rem;
  margin-bottom: 2rem;
}

.heading-container {
  display: flex;
  align-items: center;
  align-self: center;
  align-content: center;
  text-align: center;
  margin: auto;
  position: relative;
  max-width: 100%;
  flex-direction: column;
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.footer {
  background: #FBE092;
  color: #6458F5;
  text-align: center;
  padding: 1em 0;
  font-size: 1rem;
}
.footer a {
  color: inherit;
  text-decoration: none;
  font-size: 1.7rem;
}
.footer .footer-link {
  font-weight: 2rem;
}
.footer .footer-link:hover,
.footer .social-list-link:hover {
  opacity: 0.7;
}
.footer .footer-link:hover {
  text-decoration: underline;
}
.footer .social-list {
  list-style: none;
  display: flex;
  justify-content: center;
  margin: 1em 0 0;
  padding: 0;
}
.footer .social-list-item {
  margin: 0 0.2em;
}
.footer .social-list-link {
  padding: 0.5em;
}

.form-title2 {
  color: #333;
  font-family: #6458F5;
  font-size: x-large;
  font-weight: bold;
  margin-bottom: 1rem;
}

.flex-containerleft {
  text-align: left;
  margin: auto;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .flex-containerleft {
    flex-direction: row;
  }
}

.field {
  margin-bottom: 1rem;
}

.fieldleft {
  text-align: left;
  margin-bottom: 1rem;
}

.fieldleftinput {
  text-align: left;
}

.label {
  color: #333;
  font-family: "Quicksand", sans-serif;
  font-weight: bold;
}

.labelgrey {
  color: #868686;
  font-family: "Quicksand", sans-serif;
  font-weight: bold;
}

.labelgreyinput {
  color: #868686;
  font-family: "Quicksand", sans-serif;
  font-weight: bold;
}

.form-button-primary {
  background-color: #333;
  padding: 1rem;
  border-radius: 0.15rem;
  border: none;
  color: #ffffff;
  text-align: center;
  display: block;
  width: 100%;
  margin: 0 auto;
  margin-top: 0.5rem;
}
@media (min-width: 1020px) {
  .form-button-primary {
    width: 50%;
  }
}
.form-button-primary:hover {
  background-color: whitesmoke;
  border: none;
  color: #333;
}

.form-box {
  height: 1.75rem;
  width: 20rem;
  font-family: "Quicksand", sans-serif;
}

.check-box {
  font-family: "Quicksand", sans-serif;
}

.button-purple {
  cursor: pointer;
  background-color: #6458F5;
  border: none;
  border-radius: 3px;
  color: white;
  padding: 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  padding-left: 2rem;
  padding-right: 2rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
  transition: 0.2s all;
}

.button-purple:active {
  transform: scale(0.9);
}

.btn-grad {
  background-image: linear-gradient(to right, #4776E6 0%, #8E54E9 51%, #4776E6 100%);
}

.btn-grad {
  font-size: 16px;
  cursor: pointer;
  margin: 10px;
  padding: 1rem 5rem 1rem 5rem;
  text-align: center;
  transition: 0.5s;
  background-size: 200% auto;
  color: white;
  box-shadow: 0 0 20px #cacaca;
  border: none;
  border-radius: 30px;
  display: block;
  transition: 0.2s all;
  text-decoration: none;
  display: inline-block;
}

.btn-grad:hover {
  background-position: right center;
  /* change the direction of the change here */
  text-decoration: none;
  border: none;
}

.btn-grad:active {
  transform: scale(0.9);
  border: none;
}